/* eslint-disable */
import React, { useState, lazy, Suspense, memo } from 'react';
import logo from './logo.svg';
import './App.css';
import { Navbar, Nav, Container, Spinner } from 'react-bootstrap';
import { Link, Route, Switch } from 'react-router-dom';
import './main.scss';
import Data from './data.js'
import axios from 'axios';
import Main from './main.js';
let Detail = lazy(()=> import('./detail.js'));
/* import Detail from './detail.js'; */
import { propTypes } from 'react-bootstrap/esm/Image';
import Cart from './cart.js';
import Test from './test.js';
import styled from 'styled-components';
function App() {

  let [shoes, setShoes] = useState(Data);
  let [onOff, setOnOff] = useState(false);
  let [click, setClick] = useState(2);
  let [isActive, setActive] = useState(false);

  const toggleActive = () => {
    setActive(!isActive);
  }

  return (
    <div className="App">
      
      <Navbar bg="dark" variant="dark">
        <Container>
        <Navbar.Brand >WooNaN</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link as={Link} to="/">Home</Nav.Link>
          <Nav.Link as={Link} to="/detail">Product</Nav.Link>
          <Nav.Link as={Link} to="/test">Test</Nav.Link>
        </Nav>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/">
          <div className="main">
            <div className="text">  
              <h1>Injoy Your Special Chrismas</h1>
              <p>buy Christmas goods for your Special person</p>
              <button className="btn btn-danger">GIFT</button>
            </div>
          </div>

      <div className={`side-cart ${isActive ? "hide" : ""}`}>
        <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        <img src="https://codingapple1.github.io/shop/shoes2.jpg" width="100%" />
        <img src="https://codingapple1.github.io/shop/shoes3.jpg" width="100%" />
        <div className="side-btn" onClick={()=>{
          toggleActive();
        }}></div>
        <p></p>
      </div>

          <Container>
            <div className="row mt-5">
              { shoes.map((a, i)=>{
                return <Product shoes={shoes} i={i} key={i}/>
              })
              }
            </div>
            { onOff === true
              ? <Spinner className="load mt-3" animation="border" variant="danger" />
              : null
            }
            { 
              click < 4
              ? <button className="btn btn-danger my-5" onClick={()=>{
                  setOnOff(true);
                  axios.get(`https://codingapple1.github.io/shop/data${click}.json`)
                    .then((result)=>{ 
                      setShoes([...shoes, ...result.data]);
                      setOnOff(false);
                      setClick(click + 1);
                    })
                    .catch(()=>{
                      setOnOff(false);
                    })
                }}>MORE</button>
              : null
            }
          </Container>
        </Route>
        <Route path="/detail/:id">
          <Suspense fallback={<div></div>}>
              <Detail shoes={shoes}/>
          </Suspense>
        </Route>
        <Route path="/cart">
            <Cart/>
        </Route>

        <Route path="/test">
            <Test/>
        </Route>
      </Switch>









    </div>
  );
}

function Product(props) {
  return (
    <div className="col-md-4">
     <Link to={"/detail/"+(props.i)+"/"}><img src={"https://codingapple1.github.io/shop/shoes"+ (props.i + 1) +".jpg"} width="100%"/></Link>
      <h3>{props.shoes[props.i].title}</h3>
      <p>{props.shoes[props.i].content}</p>
      <p>{props.shoes[props.i].price}</p>
    </div>
  )
}

export default App;
