import React, { useState } from 'react';

function Test() {
    let [count, setCount] = useState(0);
    let [age, setAge] = useState(20);
    return (
        <div>
            <div>  {age}</div>
            <button onClick={()=>{
                setCount(count + 1);
                if (count < 3) {
                    setAge(age+1)
                }
                
                
            }}> </button>
        </div>
    )
}

export default Test;